.fixed {
  position: fixed;
  top: 60%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.pageTitle{
  text-align:center;
  font-size:3em;
  margin-top:12vh;
}
.descTxt{
  height:150px;
  overflow-y:scroll;
}
.vidContainer{
  background: orangered;
  
}

.vidWrapper{
  max-width: 400px;
  width:100%;
  overflow: hidden;
  margin: 0 auto;
}

.vidWrapper video{
  width:100%;
}

@media screen and (max-width: 800px){
  .fixed{
    top:75%;
  }
  .pageTitle{
    font-size: 1.5em;
  }
}

.vidWrapper video{
  height:250 px;
}
